@import '../App.css';
.projects {
    padding: 5%;
    height: auto;
}
.projects h1 {
    padding: 2%;
    font-size: 3em;
}
.wrapper {
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    width: 100%;
    height: auto;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
}
@media all and (max-width: 700px) {
    .wrapper {
        grid-template-columns: repeat(1, 1fr);
    }
}