@import "../App.css";
.card {
  height: 100%;
  background-color: #fff;
  padding: 1rem;
  border-radius: 5px;
  -webkit-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.25);
  -moz-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.25);
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.25);
  display: flex !important;
  flex-direction: column;
}
.projects .image {
  justify-content: center;
}
.image img {
  width: 100%;
  aspect-ratio: 7/4;
}
.image {
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 0.5rem;
  width: 100%;
}
.projects h3,
.projects h2,
.projects p,
.projects .content {
  padding: 0;
  background-color: inherit;
}
.projects {
  font-size: 1rem;
}
.projects h2 {
  font-size: 1.5rem;
}
.b {
  background-color: var(--grey-main);
  padding: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.projects-button {
    align-items: flex-end;
    
}
.projects-button a {
  display: flex;
  align-items: flex-end;
  padding-top: 2.5%;
  gap: 0.5rem;
  align-items: center;
  font-weight: 600;
}
